import { IconType } from "react-icons";
import {
  FiCompass,
  FiFeather,
  FiCheckCircle,
  FiTrendingUp,
  FiArrowRight,
  FiAward,
  FiStar,
  FiUsers,
  FiGlobe,
  FiBookOpen,
  FiTarget,
  FiUser,
  FiSend,
  FiShield,
  FiFacebook,
  FiLinkedin,
  FiYoutube,
  FiTwitter,
  FiMail,
  FiPhone,
  FiMapPin,
  FiPackage,
  FiUpload,
  FiInbox,
  FiFileText,
  FiSearch,
  FiMessageSquare,
  FiX,
} from "react-icons/fi";
import { GoOrganization } from "react-icons/go";
import { IoLeafOutline } from "react-icons/io5";
import { FaRegLightbulb, FaSimplybuilt } from "react-icons/fa6";
import { CgOptions } from "react-icons/cg";

const ICON_MAP: Record<string, IconType> = {
  FiCompass,
  FiFeather,
  FiCheckCircle,
  FiTrendingUp,
  FiArrowRight,
  FiAward,
  FiStar,
  FiUsers,
  FiGlobe,
  FiBookOpen,
  FiTarget,
  FiUser,
  FiSend,
  FiShield,
  FiFacebook,
  FiLinkedin,
  FiYoutube,
  FiTwitter,
  FiMail,
  FiPhone,
  FiMapPin,
  FiPackage,
  FiUpload,
  FiInbox,
  FiFileText,
  FiSearch,
  FiMessageSquare,
  FiX,
  GoOrganization,
  IoLeafOutline,
  FaRegLightbulb,
  FaSimplybuilt,
  CgOptions,
};


export function getIcon(icon?: string | IconType): IconType | null {
  if (!icon) return null;

  // Case 1: already a real icon component
  if (typeof icon === "function") {
    return icon;
  }

  // Case 2: string name
  if (typeof icon === "string") {
    return ICON_MAP[icon] ?? null;
  }

  return null;
}
